	module.exports = {"data":{"markdownRemark":{"html":"<p>風邪引いたorz 週末のアレで風邪を感染されたか…？</p>\n<ol>\n<li>↓…じゃなくて元々風邪気味で金土日が比較的調子よかっただけなんですが。</li>\n<li>↓万一を考えイソジンとヴィックスとプレコール買って使用</li>\n<li>↓あんまり寝れない、朝起きたら寒気までする</li>\n<li>↓会社で重要な会議があるので休めず出社</li>\n<li>↓会社でデスクトップPCの前で起きてるフリして爆睡</li>\n<li>↓そのせいかちょい病状が軽くなる</li>\n<li>↓午後になって会議、相槌だけして殆ど上の空、意識飛びかける</li>\n<li>↓会議終わって本気で意識とんだ</li>\n<li>↓早退しようとしたが病院の外来はもう終了してて断念</li>\n<li>↓薬局でベンザブロックIP購入。</li>\n<li>↓病状聞く限りインフルエンザの可能性が高いから、明日朝一番で病院行くといいよと言われる</li>\n<li>↓会社に明日休みますと電話</li>\n<li>↓部長「明後日AMに市ヶ谷まで営業にいくからそれまでにKIAIで治せ」</li>\n<li>↓インフルエンザてKIAIさえあれば24時間で治るのかそーなのかー</li>\n<li>↓電車乗ってから自宅に到着までの記憶がない</li>\n<li>↓ホット蜂蜜レモンとヴィックス交互に飲みつつひぃひぃ←今ここ</li>\n</ol>","frontmatter":{"cover":null,"date":"2007-02-20T20:01:00+09:00","strDate":"2007/2/20","title":"近況"}}},"pathContext":{"path":"/ja/blog/2007/02/20-近況/","langKey":"ja"}}

